@use 'variables' as v;
@use 'mixins' as m;
html{
    box-sizing: border-box;
    font-size: 62.5%;
}

*,*:before,*:after{
    box-sizing: inherit;
}

body{
    background: rgb(5,82,196);
    background: linear-gradient(0deg, rgba(5,82,196,0.5438550420168067) 0%, rgba(7,8,73,0.8463760504201681) 100%);
    font-family: v.$fuente-principal;
    line-height: 2.8rem;

    &.body-fijo{
        overflow: hidden;
        padding-right: 2rem;
    }

    &.espacio-barra{
        @include m.tablet{
            padding-bottom: 76px;
        }
    }
}

h1, h2, h3, h4{
    text-align: center;
    font-weight: 700;
}

h1{
    font-size: 3.6rem;
}

h2{
    font-size: 3.2rem;
}

h3{
    font-size: 2.8rem;
}

h4{
    font-size: 2.4rem;
}

p{
    font-size: 2rem;
    font-weight: 400;
}

a{
    text-decoration: none;
    margin: 0;
    padding: 0;
}

ul{
    list-style: none;
    margin: 0;
    padding: 0;
}

img{
    max-width: 100%;
    display: block;
}

//utilidades

.container{
    max-width: 120rem;
    width: 95%;
    margin: 0 auto;
}

.seccion{
    margin-top: calc(v.$separacion)/2 ;
}