@use '../base/variables' as v;
@use '../base/mixins' as m;

.contacto{
    padding-bottom: 5rem;
}

.formulario{
    .error, .enviado{
        display: inline-block;
        border-radius: 1rem;
        text-align: center;
        padding: 1rem;
        font-size: 2rem;
        font-weight: 700;
    }
    .centrar{
        display: flex;
        flex-direction: column;
        align-items: center;
        border-radius: 1rem;
    }

    .error{
        background-color: rgb(236, 150, 150);
    }

    .enviado{
        background-color: rgb(156, 196, 156);
    }
    box-shadow: 10px 15px 10px lighten($color: v.$morado, $amount: 30%);
    font-size: 2rem;
    width: min(80rem, 100%);
    margin: 0 auto;
    font-weight: 700;
    .campos{
        
        @include m.tablet{
            @include m.grid(2, 3rem);
        }
        .campo{
            padding: 1rem;
        }
        .campo:nth-child(3),
        .campo:nth-child(4){
            grid-column: 1/3;
        }
    }
    label{
        display: block;
        padding-bottom: 1rem;
    }
    
    input:not([type = "submit"]),
    textarea{
        font-weight: 700;
        padding-bottom: 1rem;
        width: 100%;
        background-color: rgba($color: #000000, $alpha: 0.2);
    }
    input::placeholder{
        color: v.$oscuro;
    }

    .enviar{
        display: flex;
        justify-content: flex-end;
        .btn-enviar{
            background-color:  lighten($color: v.$morado, $amount: 40%);
            letter-spacing: 0.5rem;
            padding: 1rem;
            border-radius: 1rem;
            font-size: 2rem;
            font-weight: 700;
            width: 100%;
           @include m.tablet{
            width: auto;
           }
            
            &:hover{
                cursor: pointer;
            }
        }
    }
}
