@use '../base/mixins' as m;
@use '../base/variables' as v;

.galeria{
    padding: 5rem 0;

    .galeria__contenido{
        @include m.grid(2, 2rem);
        @include m.tablet{
            @include m.grid(4, 2rem);
        }
    }

    picture{
        img{
            height: 100%;
        }
    }

}

.overlay{
    position: fixed;

    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    display: grid;
    place-items: center;

    background-color: rgba($color: #000000, $alpha: 0.6);
}

picture{
    max-width: 80rem;
    max-height: 40rem;
}

.btn-cerrar{
    background-color: v.$claro;
    width: 5rem;
    height: 5rem;
    border-radius: 1rem;
    font-size: 3rem;
    font-weight: 700;
    display: grid;
    place-items: center;

    &:hover{
        background-color: rgba($color: rgb(226, 77, 77), $alpha: 0.6);
        cursor: pointer;
    }
}