@use '../base/variables' as v;
@use '../base/mixins' as m;

.header{
   .video{
        position: relative;
        max-height: 60rem;
        min-height: 40rem;
        overflow: hidden;
   }

   .header__contenido{
        z-index: 1;
        position: absolute;
        width: 100%;
        height: 100%;

        h1, h2{
            color: v.$claro;
        }
        
        .legend{
            height: 25rem;
            display: flex;
            flex-direction: column;
            justify-content: center; 
            @include m.tablet{
                height: 40rem;
            }
        }
   }
}


.header__barra{
    display: flex;
    flex-direction: column;
    padding: 0 2rem;
    @include m.tablet{
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
    }
}

.navegacion{
    display: flex;
    flex-direction: column;  
    text-align: center;  
    gap: 2rem;
    .barras{
        background-image: url(/build/img/barras.svg);
        background-repeat: no-repeat;
        background-size: cover;
        width: 5rem;
        height: 6rem;
        margin: 0 auto;
        @include m.tablet{
            opacity: 0;
        }
        &:hover{
            cursor: pointer;
        }
    }
    @include m.tablet{
        flex-direction: row;
    }
    a{
        display: flex;
        justify-content: center;
        align-items: center;
        color: v.$claro;
        font-size: 1.8rem;
        visibility: hidden;
        opacity: 0;
        transition: visibility 0.1s linear,  opacity 0.5s linear;
        @include m.tablet{
            visibility: visible;
            opacity: 1;
        }
        &:hover{
            color: v.$verde;
        }
    }

}

video{
    display: block;
    width: 160rem;
    @include m.tablet{
        width: 100%;
    }
}


.icono-dark{
    display: flex;
    align-items: center;
}

.dark{
    width: 3rem;
    margin: 0 auto;
    filter: invert(100%);
    &:hover{
        cursor: pointer;
    }
}

.ocultar{
     a{
     visibility: visible;
     opacity: 1;
    } 
 }

 .nav-fijo{
    @include m.tablet{
        background-color: lighten($color: v.$gris, $amount: 10%);
        position: fixed;
        top: 0;
        right: 0;
        left: 0;
        width: 100%;
       
    }       
   }