@use '../base/mixins' as m;
@use '../base/variables' as v;

.lineup{
    padding-top: 5rem;

    p{
        text-align: center;
    }
}

.lineup__contenidos{
    @include m.tablet{
        @include m.grid(2, 3rem);
    }
}

.lineup__contenido{
    padding: 2rem;
    margin-bottom: 2rem;
    border-radius: 2rem;
    li{
        font-size: 1.8rem;
        padding: 2rem;
    }
}

.bg-verde{
    background-color: v.$verde;
    li:nth-child(odd){
        background-color: darken($color: v.$verde, $amount: 20%);
        border-radius: 1rem;
    }
}

.bg-amarillo{
    background-color: lighten($color: v.$morado, $amount: 40%);
    li:nth-child(odd){
        background-color: lighten($color: v.$morado, $amount: 30%);
        border-radius: 1rem;
    }
}

.edm, .rock{
    .titulo::after{
        content: '';
        display: block;
        width: 20rem;
        height: 20rem;
        margin: 2rem auto 0;
        background-size: 25rem;
        background-repeat: no-repeat;
        background-position: center center;
    }
}

.edm{
    .titulo::after{
        background-image: url(../../../build/img/icono_edm.png);
    }
}

.rock{
    .titulo::after{
        background-image: url(../../../build/img/icono_rock.png);
    }
}