html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  background: rgb(5, 82, 196);
  background: linear-gradient(0deg, rgba(5, 82, 196, 0.543855042) 0%, rgba(7, 8, 73, 0.8463760504) 100%);
  font-family: "Montserrat", sans-serif;
  line-height: 2.8rem;
}
body.body-fijo {
  overflow: hidden;
  padding-right: 2rem;
}
@media (min-width: 768px) {
  body.espacio-barra {
    padding-bottom: 76px;
  }
}

h1, h2, h3, h4 {
  text-align: center;
  font-weight: 700;
}

h1 {
  font-size: 3.6rem;
}

h2 {
  font-size: 3.2rem;
}

h3 {
  font-size: 2.8rem;
}

h4 {
  font-size: 2.4rem;
}

p {
  font-size: 2rem;
  font-weight: 400;
}

a {
  text-decoration: none;
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  display: block;
}

.container {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto;
}

.seccion {
  margin-top: 5rem/2;
}

.oscuro {
  background: rgb(29, 28, 28);
}
.oscuro .dark {
  filter: invert(60%);
}
.oscuro h1, .oscuro h2, .oscuro h3, .oscuro p {
  color: #fff;
}
.oscuro .btn-cerrar {
  color: rgb(29, 28, 28);
}
.oscuro .formulario {
  box-shadow: 5px 10px 15px #22c6db;
}
.oscuro .formulario .enviar .btn-enviar {
  background-color: #22c6db;
  color: #000;
}
.oscuro .formulario, .oscuro input, .oscuro textarea, .oscuro .enviar {
  color: #22c6db;
}
.oscuro .formulario input::placeholder, .oscuro input input::placeholder, .oscuro textarea input::placeholder, .oscuro .enviar input::placeholder {
  color: #22c6db;
}
.oscuro .error, .oscuro .enviado {
  color: #000;
}

.header .video {
  position: relative;
  max-height: 60rem;
  min-height: 40rem;
  overflow: hidden;
}
.header .header__contenido {
  z-index: 1;
  position: absolute;
  width: 100%;
  height: 100%;
}
.header .header__contenido h1, .header .header__contenido h2 {
  color: #fff;
}
.header .header__contenido .legend {
  height: 25rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@media (min-width: 768px) {
  .header .header__contenido .legend {
    height: 40rem;
  }
}

.header__barra {
  display: flex;
  flex-direction: column;
  padding: 0 2rem;
}
@media (min-width: 768px) {
  .header__barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}

.navegacion {
  display: flex;
  flex-direction: column;
  text-align: center;
  gap: 2rem;
}
.navegacion .barras {
  background-image: url(/build/img/barras.svg);
  background-repeat: no-repeat;
  background-size: cover;
  width: 5rem;
  height: 6rem;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .navegacion .barras {
    opacity: 0;
  }
}
.navegacion .barras:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .navegacion {
    flex-direction: row;
  }
}
.navegacion a {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  font-size: 1.8rem;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.1s linear, opacity 0.5s linear;
}
@media (min-width: 768px) {
  .navegacion a {
    visibility: visible;
    opacity: 1;
  }
}
.navegacion a:hover {
  color: rgba(37, 194, 191, 0.7333333333);
}

video {
  display: block;
  width: 160rem;
}
@media (min-width: 768px) {
  video {
    width: 100%;
  }
}

.icono-dark {
  display: flex;
  align-items: center;
}

.dark {
  width: 3rem;
  margin: 0 auto;
  filter: invert(100%);
}
.dark:hover {
  cursor: pointer;
}

.ocultar a {
  visibility: visible;
  opacity: 1;
}

@media (min-width: 768px) {
  .nav-fijo {
    background-color: #373535;
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    width: 100%;
  }
}

.festival {
  padding-top: 5rem;
}

@media (min-width: 768px) {
  .festival__contenido {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem;
  }
}
.festival__contenido .festival__bg {
  font-weight: 700;
  background-color: #22c6db;
  display: inline-block;
}

.festival__imagen img {
  height: 100%;
}

.lineup {
  padding-top: 5rem;
}
.lineup p {
  text-align: center;
}

@media (min-width: 768px) {
  .lineup__contenidos {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem;
  }
}

.lineup__contenido {
  padding: 2rem;
  margin-bottom: 2rem;
  border-radius: 2rem;
}
.lineup__contenido li {
  font-size: 1.8rem;
  padding: 2rem;
}

.bg-verde {
  background-color: rgba(37, 194, 191, 0.7333333333);
}
.bg-verde li:nth-child(odd) {
  background-color: rgba(21, 108, 107, 0.7333333333);
  border-radius: 1rem;
}

.bg-amarillo {
  background-color: #9d63c3;
}
.bg-amarillo li:nth-child(odd) {
  background-color: #8444b0;
  border-radius: 1rem;
}

.edm .titulo::after, .rock .titulo::after {
  content: "";
  display: block;
  width: 20rem;
  height: 20rem;
  margin: 2rem auto 0;
  background-size: 25rem;
  background-repeat: no-repeat;
  background-position: center center;
}

.edm .titulo::after {
  background-image: url(../../../build/img/icono_edm.png);
}

.rock .titulo::after {
  background-image: url(../../../build/img/icono_rock.png);
}

.galeria {
  padding: 5rem 0;
}
.galeria .galeria__contenido {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 768px) {
  .galeria .galeria__contenido {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
.galeria picture img {
  height: 100%;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.6);
}

picture {
  max-width: 80rem;
  max-height: 40rem;
}

.btn-cerrar {
  background-color: #fff;
  width: 5rem;
  height: 5rem;
  border-radius: 1rem;
  font-size: 3rem;
  font-weight: 700;
  display: grid;
  place-items: center;
}
.btn-cerrar:hover {
  background-color: rgba(226, 77, 77, 0.6);
  cursor: pointer;
}

.contacto {
  padding-bottom: 5rem;
}

.formulario {
  box-shadow: 10px 15px 10px #8444b0;
  font-size: 2rem;
  width: min(80rem, 100%);
  margin: 0 auto;
  font-weight: 700;
}
.formulario .error, .formulario .enviado {
  display: inline-block;
  border-radius: 1rem;
  text-align: center;
  padding: 1rem;
  font-size: 2rem;
  font-weight: 700;
}
.formulario .centrar {
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 1rem;
}
.formulario .error {
  background-color: rgb(236, 150, 150);
}
.formulario .enviado {
  background-color: rgb(156, 196, 156);
}
@media (min-width: 768px) {
  .formulario .campos {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem;
  }
}
.formulario .campos .campo {
  padding: 1rem;
}
.formulario .campos .campo:nth-child(3),
.formulario .campos .campo:nth-child(4) {
  grid-column: 1/3;
}
.formulario label {
  display: block;
  padding-bottom: 1rem;
}
.formulario input:not([type=submit]),
.formulario textarea {
  font-weight: 700;
  padding-bottom: 1rem;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.2);
}
.formulario input::placeholder {
  color: #000;
}
.formulario .enviar {
  display: flex;
  justify-content: flex-end;
}
.formulario .enviar .btn-enviar {
  background-color: #9d63c3;
  letter-spacing: 0.5rem;
  padding: 1rem;
  border-radius: 1rem;
  font-size: 2rem;
  font-weight: 700;
  width: 100%;
}
@media (min-width: 768px) {
  .formulario .enviar .btn-enviar {
    width: auto;
  }
}
.formulario .enviar .btn-enviar:hover {
  cursor: pointer;
}

.footer p {
  text-align: center;
}