@use '../base/variables' as v;

.oscuro{
        background: v.$gris;
  
    .dark{
        filter: invert(60%);
    }

    h1, h2, h3, p {
        color: v.$claro;
    }
    .btn-cerrar{
        color: v.$gris;
    }
    .formulario{
        box-shadow: 5px 10px 15px lighten($color: v.$amarillo_claro,$amount: 0%); 
        .enviar{
            .btn-enviar{
                background-color: v.$amarillo_claro;
                color: v.$oscuro;
                }
       }
    }

    .formulario,input,textarea,.enviar{
        color: v.$amarillo_claro;
        input::placeholder{
            color: v.$amarillo_claro;
        }
   }

   .error, .enviado{
    color: v.$oscuro;
   }
  
}